
:root {
  --card-scale: 1.45;
}

.card {
  font-family: 'Inter';
  width: calc(550px * var(--card-scale));
  box-shadow: 1px 1px 10px #00000026;
  border-radius: 0%;
  display: flex;
  flex-direction: column;
  aspect-ratio: 1 / 0.65;
  overflow: hidden;
 }

 .card__info {
  display: flex;
  flex-direction: column;
  width: 100%;
 }

 .card__container {
  display: flex;
  column-gap: calc(20px * var(--card-scale));
  box-sizing: border-box;
  justify-content: flex-start;
  max-height: 100%;
}

.row {
  display: flex;
  justify-content: space-between;
}


.card__image img {
  width: calc(238px * var(--card-scale));
  height: calc(357px * var(--card-scale));
}

.card__title h5 {
  font-size: calc(20px * var(--card-scale));
  line-height: calc(30px * var(--card-scale));
  font-weight: 400;
  margin: calc(0.4em * var(--card-scale)) 0;
  margin-bottom: 0;
}

.rated {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid;
  flex: 0 0 auto;
  margin: 4px 4px 4px 6px;
}

.card__release {
  font-size: calc(15px * var(--card-scale));
  font-weight: 400;
  line-height: calc(17px * var(--card-scale));
  color: #827E7E;

}

.card__genre ul {
  display: flex;
  gap: calc(11px * var(--card-scale));
  margin: 0;
}


.card__genre li {
  font-size: calc(14px * var(--card-scale));
  font-weight: 400;
  line-height: calc(14px * var(--card-scale));
  color: #000000A6;
  border: 1px solid #D9D9D9;
  border-radius: 8%;
  padding: calc(4px * var(--card-scale));

}

.card__shortDescription {
  font-size: calc(14px * var(--card-scale));
  font-weight: 400;
  line-height: calc(22px * var(--card-scale));
  color: #000000;
  margin: calc(19px * var(--card-scale));
  margin-left: 0;
  max-height: 100%;
  scroll-behavior: smooth;

}

.rate {
  position: absolute;
  bottom: 15px;
  margin: 0 auto;
}

@media screen and (max-width: 1828px) {
  :root {
    --card-scale: 1.3;
  }
}

@media screen and (max-width: 1644px) {
  :root {
    --card-scale: 1;
  }
}